  --badge-bg-color: var(--color-bg-system);
  --badge-border-color: var(--color-bg-system)
}

.badge1_view_filled {
  color: var(--color-typo-primary);
  background: var(--badge-bg-color)
}

.badge1_view_filled.badge1_status_system {
  --badge-text-color: var(--color-typo-secondary)
}

.badge1_view_stroked {
  color: var(--badge-text-color);
  -webkit-box-shadow: inset 0 0 0 1px var(--badge-border-color);
  box-shadow: inset 0 0 0 1px var(--badge-border-color)
}

.badge1_view_stroked.badge1_status_system {
  --badge-text-color: var(--color-typo-system)
}

.badge1_minified {
  --badge-text-size: 0;
  border-radius: 50%;
  background: transparent
}

.badge1_minified.badge1_size_s {
  width: 12px;
  height: 12px;
  border: 1px solid transparent
}

.badge1_minified.badge1_size_m,
.badge1_minified.badge1_size_s {
  padding: 0;
  -webkit-box-shadow: inset 0 0 0 10px var(--badge-bg-color);
  box-shadow: inset 0 0 0 10px var(--badge-bg-color)
}

.badge1_minified.badge1_size_m {
  width: 16px;
  height: 16px;
  border: 2px solid transparent
}

.badge1__icon {
  font-size: 0;
  line-height: 1em
}